'global.home.title':'e石e石，亿链基石','global.home.content':'e石，是建立在“边缘共识异步分片智能合约协议”基础上的公有链，是未来dApp开发和运行的平台。e石的英文名eRoc源自对Core的反写，寓意我们将创造一个更加开放、平等、自治的平台。','global.home.btn':'白皮书','global.home.explain':'以技术创新为例，建立起亿万产业链的基石','global.home.eroc':'eRoc 是什么?',// 六大优势
'global.home.advantage1.title':'独创快速共识算法，确保不错过每个商机','global.home.advantage1.content':'全球独创的去中心化公有链共识算法DRC：确定性随机共识机制（Deterministic-Random Consensus, 简称DRC，已经申请专利），创造性的实现了“确定性”、“随机性”两大水火不容之物的相融；DRC共识算法，由不可外包工作量证明机制（PoWFOP）等众多先进算法和机制来保证和实现，没有过多算力消耗问题，真正的平民化、零门槛，从速度、效率、门槛、经济性、社会意义等角度优于POW,POS,DPOS等共识算法。','global.home.advantage2.title':'数据分片存储，量再大也不怕','global.home.advantage2.content':'做为公链，随着时间推移，数据越来越多，存储面临着巨大的挑战。EROC独创智能分片式存储技术（已申请专利），一劳永逸的解决了公链数据不断增长的问题，使得海量数据的存储瓶颈不再成为公链发展的障碍，成为了极少数在创立之初就引入数据长期存储管理机制的稀缺公链。','global.home.advantage3.title':'合约分片运营，从容应对交易高峰','global.home.advantage3.content':'完全基于自有技术的先进智能合约分片式运行机制，这种机制对整个网络的节点实行智能分片，形成多中心。每个分中心都同时执行不同的智能合约。通过节点级别并行的方式，极大的提高了智能合约的执行效率，增强了系统的吞吐能力。DRC共识机制结合智能合约分片式运行机制使得整个EROC公链系统具有可控的计算竞争和高效的系统性能，在系统效率、竞争压力、系统安全三方面达成了完美纳什均衡。','global.home.advantage5.title':'支持多语言开发，吸引全球99%的开发者','global.home.advantage5.content':'EROC支持更多开发者的智能合约开发体系。EROC公链将支持Java，JavaScript，Go，C++等主流语言开发智能合约，已经覆盖了全球99%的开发者，为公链未来生态的发展打下了坚实的基础；','global.home.advantage4.title':'微内核技术让高深的技术进入组装年代','global.home.advantage4.content':'英特尔将电脑模块化，人人都可以用搭积木一样组装属于自己的电脑，成为电脑进入千家万户的关键一步。亿石EROC采用微内核技术，让区块链迅速适配各个行业的需求，正式进入个性化组装年代。EROC独有微内核架构。微内核（Micro Kernel）是提供公链系统核心功能的精简集合，它把公链最核心的功能，如账本存储、共识、智能合约调度作为微内核的核心，其他系统服务如节点管理、账号体系、安全体系等都以系统智能合约的方式实现，提高了整个系统的灵活性、鲁棒性、可延展性。','global.home.advantage6.title':'完全去中心化充分享受区块链红利','global.home.advantage6.content':'比特币算力越来越集中，以太坊算力也有集中的趋势，区块链的超级节点竞选使得其公开走向中心化。而区块链中心化的趋势会带来易被攻击等诸多隐患。为了充分享有去中心化所带来的红利，EROC公链坚持去中心化方向，同时采用DRC、PoWFOP等技术解决去中心化所带来的挑战。',// path
'global.home.path.title':'一个具有伟大功能的路线图。趁我们还在成长，加入我们吧!','global.home.path1.title':'白皮书','global.home.path1.content':'基于自身技术的高级智能契约的异步分片执行机制，对整个网络节点实现智能分片，形成多中心。','global.home.path1.time':'4 June 2018','global.home.path2.title':'私人配售','global.home.path2.content':'','global.home.path2.time':'4 June 2018','global.home.path3.title':'路演','global.home.path3.content':'基于自身技术的高级智能契约的异步分片执行机制，对整个网络节点实现智能分片，形成多中心。','global.home.path3.time':'4 June 2018','global.home.path4.title':'交换网络','global.home.path4.content':'','global.home.path4.time':'4 June 2018','global.home.path5.title':'公共区块链测试','global.home.path5.content':'异步分片执行','global.home.path5.time':'4 June 2018','global.home.path6.title':'公共区块链释放','global.home.path6.content':'','global.home.path6.time':'4 June 2018',// 订阅
